// setup the worker environment and key functions
// importScripts('http://localhost:8080/ace/worker/worker-base.js'); // non-webpack
import workerBase from 'raw-loader!ace/worker/worker-base';
eval.call(null, workerBase);

// this import syntax stops webpack renaming them, because otherwise
// it doesn't recognise usage inside the AMD define block
const antlr4 = require('antlr4');
const AnnotatingErrorListener = require('grammar/AnnotatingErrorListener').default;
const grammar = require('grammar/devfactory/e2e');

define("ace/mode/antlr4_worker", [], function(require, exports, module) {
"use strict";

var oop = require("ace/lib/oop");
var Mirror = require("ace/worker/mirror").Mirror;

var Antlr4Worker = exports.Antlr4Worker = function(sender) {
    Mirror.call(this, sender);
    this.setTimeout(200);
    // todo: load grammar dynamically based on value passed from mode
    this.grammar = grammar;
};
oop.inherits(Antlr4Worker, Mirror);

(function() {

    this.validate = function(input) {
        if (!this.grammar) return [];

        var stream = antlr4.CharStreams.fromString(input);
        var lexer = new grammar.Lexer(stream);
        var tokens = new antlr4.CommonTokenStream(lexer);
        var parser = new grammar.Parser(tokens);
        var listener = new AnnotatingErrorListener();

        lexer.removeErrorListeners();
        lexer.addErrorListener(listener);
        parser.removeErrorListeners();
        parser.addErrorListener(listener);

        parser[grammar.entryPoint]();

        return listener.annotations;
    };

    this.setGrammar = function(grammar) {
        console.error("Not implemented yet!");
        // importScripts('http://localhost:8080/' + grammar);
        // this.grammar = ?
    };

    this.onUpdate = function() {
        var value = this.doc.getValue();
        var annotations = this.validate(value);
        this.sender.emit("annotate", annotations);
    };

}).call(Antlr4Worker.prototype);

});
